.full-project{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 300px;
    background-color: #0e1111;
    margin-bottom: 20px;
    & .project-details{
        width: 50%;
    }
    & img{
        width: 300px;
        height: 300px;
        object-fit: cover;
    }
}
@media (max-width: 600px){
    .full-project{
        flex-direction: column;
        height: max-content;
        text-align: center;
        & .project-details{
            width: 100%;
        }
        & img{
            width: 100%;
            height: 100%;
        }
    }
}